<template>
    <div class="order">
        <div class="order_search">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="查询方式">
                    <el-select v-model="formInline.region" placeholder="请选择查询方式">
                    <el-option label="订单编号" value="id"></el-option>
                    <el-option label="用户手机号" value="mobile"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="内容">
                    <el-input v-model="formInline.searchIup" placeholder="请输入内容"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
            v-loading="loading"
            :data="tableData5"
            style="width: 100%">
            <el-table-column type="expand">
            <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="商品编号">
                        <span>{{ props.row.order_oid }}</span>
                    </el-form-item>
                    <el-form-item label="用户编号">
                        <span>{{ props.row.user_id }}</span>
                    </el-form-item>
                    <el-form-item label="商品类型">
                        <span>{{ props.row.comm_type }}</span>
                    </el-form-item>
                    <el-form-item label="商品名称">
                        <span>{{ props.row.order_name }}</span>
                    </el-form-item>
                    <el-form-item label="房屋面积">
                        <span>{{ props.row.order_per }}</span>
                    </el-form-item>
                    <el-form-item label="预约时间">
                        <span>{{ props.row.make_date }}</span>
                    </el-form-item>
                    <el-form-item label="下单时间">
                        <span>{{ props.row.place_time }}</span>
                    </el-form-item>
                    <el-form-item label="小区地址">
                        <span>{{ props.row.user_addree }}</span>
                    </el-form-item>
                    <el-form-item label="号楼门牌">
                        <span>{{ props.row.user_num }}</span>
                    </el-form-item>
                    <el-form-item label="用户姓名">
                        <span>{{ props.row.user_name }}</span>
                    </el-form-item>
                    <el-form-item label="手机号码">
                        <span>{{ props.row.user_mobile }}</span>
                    </el-form-item>
                    <el-form-item label="支付方式">
                        <span v-if="props.row.play_way == 0">微信支付</span>
                        <span v-if="props.row.play_way == 1">线下支付</span>
                    </el-form-item>
                    <el-form-item label="支付价格">
                        <span>{{ props.row.order_price }}</span>
                    </el-form-item>
                    <el-form-item label="接单人">
                        <span>{{ props.row.project }}</span>
                    </el-form-item>
                </el-form>
            </template>
            </el-table-column>
            <el-table-column
            label="商品编号"
            prop="order_oid">
            </el-table-column>
            <el-table-column
            label="下单时间"
            prop="place_time">
            </el-table-column>
            <el-table-column
            label="商品类型"
            prop="comm_type">
            </el-table-column>
            <el-table-column
            label="支付方式">
                <template slot-scope="scope">
                    <span v-if="scope.row.play_way == 0">微信支付</span>
                    <span v-if="scope.row.play_way == 1">线下支付</span>
                </template>
            </el-table-column>
            <el-table-column
            label="订单状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.order_type == 0">未支付</span>
                    <span v-if="scope.row.order_type == 1">进行中</span>
                    <span v-if="scope.row.order_type == 2">已完成</span>
                    <span v-if="scope.row.order_type == 3">已取消</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row.order_oid)">接单</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDone(scope.$index, scope.row.order_oid)">完成</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    v-if="roles == 'admin'"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,15,20]"
                    :page-size="pagesize"
                    :total="total"    
                    layout="total, sizes, prev, pager, next, jumper">    
            </el-pagination>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .order {
        padding: 20px;
    }
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .page {
        padding: 30px 0;
        float: right;
    }
</style>

      
<script>
export default {
    data() {
        return {
            loading: true,
            tableData5: [],
            roles:"", // 权限判断参数
            currentPage:1, //初始页
            pagesize:20,    //    每页的数据
            total:3, // 订单列表总数量
            formInline:{
                searchIup:"", // 搜索内容
                region:'',// 搜索类型
            }
            
        }
    },
    mounted() {
        //this.orderList();   
        this.PageOrderList(); // 分页订单列表
        this.roles = this.$store.state.userInfo.roles;
    },
    methods: {
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
            this.pagesize = size;
            this.PageOrderList(); // 分页订单列表
            console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
            this.PageOrderList(); // 分页订单列表
            console.log(this.currentPage)  //点击第几页
        },
        PageOrderList () {
            let that = this;
            let url = '/api'+this.$common.order_page;
            let data = {
                offset:that.currentPage,
                limit:that.pagesize
            }
            that.$http.post(url,data).then((res)=>{
                console.log(res)
                if ( res.code == '200' ) {
                    that.tableData5 = res.data;
                    that.total = res.num;
                    setTimeout(function(){
                        that.loading = false;
                    },2000)
                }
            }).catch(data=>{

            })
        },
        orderList () {
            let url = '/api'+this.$common.order_all;
            let that = this;
            this.$http.post(url).then((res)=>{
                console.log(res)
                if ( res.code == '200' ) {
                    that.tableData5 = res.data;
                    setTimeout(function(){
                        that.loading = false;
                    },2000)
                } else if( res.code == '404' ){
                    sessionStorage.clear();
                    that.$router.push({ path:'/login' });
                }   
            }).catch(data=>{

            })
        },
        handleEdit (index,order_oid) {
            let that = this;
            this.loading = true;
            that.$prompt('请输入接单人姓名', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                console.log(value)
                let url = '/api'+that.$common.project;
                let data = ({
                    order_oid:order_oid,
                    project:value
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.code == 1){
                        setTimeout(() => {
                            this.loading = false;
                        },2000)
                        this.$message({
                            message: '增加接单人成功',
                            type: 'success'
                        });
                    }else {
                        this.$message({
                            message: '增加接单人失败',
                            type: 'success'
                        }); 
                    }  
                }).catch(res=>{
                    this.$message({
                        message: '增加接单人失败',
                        type: 'success'
                    });
                })
            }).catch(() => {
                      
            });
        },
        handleDone (index,order_oid) {
            let that = this;
            this.loading = true;
            this.$confirm('是否已经完成订单', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                let url = '/api'+that.$common.done;
                let data = ({
                    order_oid:order_oid,
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 1){
                        setTimeout(function(){
                            this.loading = false;
                        },2000)
                        that.$message({
                            message: '完成订单',
                            type: 'success'
                        });
                    }else {
                        that.$message({
                            message: '完成订单失败',
                            type: 'success'
                        }); 
                    }  
                }).catch(res=>{
                    this.$message({
                        message: '完成订单失败',
                        type: 'success'
                    });
                })   
            }).catch(() => {
                          
            });
            
        },
        onSubmit () {
           
            let that = this;
            let url;
            let data = ({
                order_oid:that.formInline.searchIup,
            })
            this.loading = true;
            if (that.formInline.region == 'id') {
                url = '/api'+that.$common.orderSearchId;
            } else if (this.formInline.region == 'mobile') {
                url = '/api'+that.$common.orderSearchMobile;
            }
            if (that.formInline.searchIup == '') {
                this.orderList(); 
                setTimeout(() => {
                    this.loading = false;
                },2000)
                
            } else {
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 1){
                        setTimeout(() => {
                            this.loading = false;
                        },2000)
                        
                        this.tableData5 = res.data;
                    }
                }).catch(res=>{
                    this.$message({
                        message: '搜索失败',
                        type: 'success'
                    });
                })
            }
            

        }
    },
}
</script>


